const $siteList = $('.siteList');
const $lastLi = $siteList.find('li.last');
const x = localStorage.getItem('x');
const xObject = JSON.parse(x);
// Âª∫Á´ãÂìàÂ∏åË°®ÔºåÂÇ®Â≠òÁΩëÁ´ô‰ø°ÊÅØ
const hashMap = xObject || [
    { logo: 'A', logoType: 'text', url: 'https://www.acfun.cn' },
    { logo: 'B', logoType: 'text', url: 'https://www.bilibili.cn' },
    { logo: 'S', logoType: 'text', url: 'https://www.sina.com.cn' },
    { logo: 'B', logoType: 'text', url: 'https://www.baidu.com' },
    { logo: 'J', logoType: 'text', url: 'https://www.jianshu.com' },
]

// ÂéªÈô§ÊòæÁ§∫ËøûÊé•ÂâçÁºÄ
const simplifyUrl = (url) => {
    // Âà†Èô§/ÂºÄÂ§¥ÁöÑÂÜÖÂÆπ
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace('/', '')
}

const render = () => {
    $siteList.find('li:not(.last)').remove()
    hashMap.forEach((node, index) => {
        // Ê∑ªÂä†ÁΩëÂùÄ
        const $li = $(`
            <li>
                <div class="site">
                    <div class="logo">${node.logo[0]}</div>
                    <div class="link">${simplifyUrl(node.url)}</div>
                    <div class="close">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-close"></use>
                        </svg>
                    </div>
                </div>
            </li>
        `
        ).insertBefore($lastLi);

        $li.on('click', () => {
            window.open(node.url)
        })

        // Âà†Èô§ÁΩëÂùÄ
        $li.on('click', '.close', (e) => {
            e.stopPropagation()
            hashMap.splice(index, 1);
            render();
        })
    })
};
render();

$('.addButton').on('click', () => {
    let url = window.prompt('ËØ∑ËæìÂÖ•ÈúÄË¶ÅÊñ∞Â¢ûÁöÑÁΩëÂùÄÔºö')
    if (url.indexOf('http') !== 0) {
        url = 'https://' + url;
    };
    hashMap.push({
        logo: simplifyUrl(url)[0].toUpperCase(),
        logoType: 'text',
        url: url
    });
    render();
})


window.onbeforeunload = () => {
    // JSON.stringifyÂèØ‰ª•Â∞Ü‰∏Ä‰∏™ÂØπË±°ÂèòÊàêÂ≠óÁ¨¶‰∏≤
    const string = JSON.stringify(hashMap);
    // Âú®Êú¨Âú∞localStorageÈáåËÆæÁΩÆ‰∏Ä‰∏™xÔºå‰ªñÁöÑÂÜÖÂÆπÂ∞±ÊòØstring
    localStorage.setItem('x', string);
}


// ÈîÆÁõòÁõëÂê¨
$(document).on('keypress', (e) => {
    const { key } = e;
    for (let i = 0; i < hashMap.length; i++) {
        if (hashMap[i].logo.toLowerCase() === key) {
            window.open(hashMap[i].url)
        }
    }
})